<template>
  <div class="more">
    <el-backtop></el-backtop>
    <van-nav-bar>
      <template #title>
        <div class="square-title">{{ title }}</div>
      </template>
      <template #left>
        <div class="right-icon">
          <img
            src="../assets/images/left.png"
            alt
            class="auto-img"
            @click="goback"
          />
        </div>
      </template>
    </van-nav-bar>
    <div class="rank" v-for="(item, index) in showList" :key="index">
      <div class="rank-list">
        <div class="rank-list-left">
          <img :src="item.cover" alt="" class="auto-img" />
        </div>
        <div class="rank-list-right">
          <div class="rank-name">{{ item.name }}</div>
          <div class="rank-author">更新至：{{ item.latest }}</div>
          <div class="rank-text clearfix">
            <div class="text-time fl">{{ item.time }}</div>
            <div class="text-icon fr" @click="goDetail(item.url)">
              <img
                src="../assets/images/yuedu.png"
                alt=""
                class="auto-img book-icon"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //标题
      title: "",
      //链接
      mhlb: "",
      // 数据列表
      showList: "",
      // 分类
      find: "",
    };
  },
  created() {
    // 获取数据
    this.getData();
  },
  methods: {
    // 获取数据
    getData() {
      
      // 获取标题
      this.title = this.$route.query.title;
      // 获取数据列表
      this.mhlb = this.$route.query.mhlb;
      // 获取分类标识
      this.find = this.$route.query.find;
      
      this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhlb: this.mhlb,
        },
      })
        .then((res) => {
          this.$toast.clear();
          let resList = res.data.list;
          resList.shift();
          this.showList = resList;
        })
        .catch(() => {
          this.$toast.clear();
        });
    },
    // 返回首页
    goback() {
      if (this.find) {
        this.$router.push({ name: "Find" });
        return;
      }
      this.$router.push({ name: "Home" });
    },
    // 详情
    goDetail(url) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      setTimeout(() => {
        this.$toast.clear();
        this.$router.push({
          name: "Detail",
          query: {
            bookurl: url,
            title: this.title,
            logo: "more",
            mhlb: this.mhlb,
            find: this.find,
          },
        });
      }, 2000);
    },
  },
};
</script>

<style lang="less" scoped>
.more {
  .right-icon {
    width: 24px;
    height: 24px;
  }

  .square-title {
    font-size: 18px;
    color: #016831;
  }

  /deep/ .van-nav-bar {
    background: #e8dddb;
  }

  .rank {
    padding: 15px;
    border-bottom: 1px solid #f0f0e6;
    &:nth-child(1) {
      border-top: 1px solid #f0f0e6;
    }
  }

  .rank-list {
    display: flex;
  }

  .rank-list-left {
    width: 90px;
    height: 120px;
    border-radius: 5px;
    background: url("https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=3909803694,1786385326&fm=26&gp=0.jpg")
      no-repeat;
    background-size: 90px 120px;
  }

  .rank-list-right {
    flex: 1;
    padding-left: 15px;
  }

  .rank-name {
    width: 240px;
    height: 40px;
    font-size: 16px;
    line-height: 40px;
    padding-top: 10px;
    color: #494949;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .rank-author {
    width: 240px;
    height: 40px;
    font-size: 14px;
    line-height: 40px;
    color: #494949;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .rank-text {
    height: 30px;
    font-size: 12px;
    line-height: 30px;
    color: #a4a4a4;
  }

  .el-backtop {
    background: #016831;
    width: 30px;
    height: 30px;
  }

  /deep/ .el-icon-caret-top:before {
    content: "\e78f";
    color: #50b9a4;
    font-size: 24px;
  }

  .text-icon {
    width: 30px;
    height: 30px;
    margin-right: 50px;
  }

  .book-icon {
    height: 30px;
  }
}
</style>